import { useState, useEffect } from "react";
import { Outlet, Navigate } from "react-router-dom";
import DashboardLayout from "../ComponentChilds/Dashboard/DashboardLayout";

function getCookie(name) {
  const value = `; ${document.cookie}`;
  const parts = value.split(`; ${name}=`);
  if (parts.length === 2) return parts.pop().split(";").shift();
  return null; // return null if the cookie is not found
}

const Dashboard = () => {
  const [role, setRole] = useState(null);
  const [loading, setLoading] = useState(true);

  const API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/user/"
      : "https://allmaxsport.com/api/user/";

  useEffect(() => {
    const userID = getCookie("userId");
    const fetchUserRole = async () => {
      const response = await fetch(`${API_URL}?id=${userID}`);
      const data = await response.json();
      const userRole = data[0].user_type;
      setRole(userRole);
      setLoading(false);
    };

    fetchUserRole();
  }, []);

  if (loading) {
    return (
      <div
        dir="rtl"
        className="min-h-screen flex items-center justify-center bg-gray-50 font-vazir"
      >
        <p className="text-lg text-gray-700">در حال بارگذاری...</p>
      </div>
    );
  }

  if (!role) {
    return <Navigate to="/auth" replace />;
  }

  return (
    <DashboardLayout role={role}>
      <Outlet context={{ role }} />
    </DashboardLayout>
  );
};

export default Dashboard;
