import { useState, useEffect } from "react";
import {
  Home,
  Package,
  Users,
  ClipboardList,
  Newspaper,
  Mail,
  User,
  ArrowRightCircle,
  Sun,
  Moon,
  LogOut,
  HomeIcon,
} from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { Link, useLocation, Navigate, useNavigate } from "react-router-dom";
import toast from "react-hot-toast";

function removeCookie(name) {
  document.cookie = name + "=; Max-Age=0; path=/;";
}

// Navigation items for each role
const adminNavItems = [
  { label: "داشبورد", icon: <Home size={20} />, path: "/dashboard" },
  {
    label: "محصولات",
    icon: <Package size={20} />,
    path: "/dashboard/products",
  },
  {
    label: "سفارشات",
    icon: <ClipboardList size={20} />,
    path: "/dashboard/orders",
  },
  { label: "کاربران", icon: <Users size={20} />, path: "/dashboard/users" },
  { label: "بلاگ", icon: <Newspaper size={20} />, path: "/dashboard/blogs" },
  { label: "تیکت‌ها", icon: <Mail size={20} />, path: "/dashboard/tickets" },
  { label: "بازگشت به خانه", icon: <HomeIcon size={20} />, path: "/" },
];

function getCookie(name) {
  const value = `; ${document.cookie}`;
  const parts = value.split(`; ${name}=`);
  if (parts.length === 2) return parts.pop().split(";").shift();
  return null; // return null if the cookie is not found
}

const userNavItems = [
  {
    label: "سفارشات",
    icon: <ClipboardList size={20} />,
    path: "/dashboard/orders",
  },
  { label: "تیکت‌ها", icon: <Mail size={20} />, path: "/dashboard/tickets" },
  { label: "پروفایل", icon: <User size={20} />, path: "/dashboard/profile" },
  {
    label: "بازگشت به خانه",
    icon: <HomeIcon size={20} />,
    path: "/",
  },
];

const DashboardLayout = ({ children }) => {
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [loading, setLoading] = useState(true);
  const location = useLocation();
  const navigate = useNavigate();

  const [role, setRole] = useState(null);
  useEffect(() => {
    const API_URL =
      window.location.hostname === "localhost"
        ? "http://127.0.0.1:8000/api/user/"
        : "https://allmaxsport.com/api/user/";

    const userID = getCookie("userId");
    const fetchUserRole = async () => {
      const response = await fetch(`${API_URL}?id=${userID}`);
      const data = await response.json();
      const userRole = data[0].user_type;
      setRole(userRole);
      setLoading(false);
    };

    fetchUserRole();
  }, []);

  const handleLogout = () => {
    try {
      removeCookie("userId");
      toast.success("با موفقیت خارج شدید", {
        duration: 1000,
        position: "top-center",
      });
      setTimeout(() => {
        navigate("/auth");
      }, 1000);
    } catch (error) {
      toast.error("خطا در خروج", {
        position: "top-center",
      });
    }
  };
  // Function to check if a path is active
  const isActive = (path) => {
    return (
      location.pathname === path ||
      (path !== "/" &&
        path !== "/dashboard" &&
        location.pathname.startsWith(path))
    );
  };

  // Select navigation items based on role
  const navItems = role === "user" ? userNavItems : adminNavItems;

  // Loading state
  if (loading) {
    return (
      <div
        dir="rtl"
        className="min-h-screen flex items-center justify-center bg-gray-50 font-vazir"
      >
        <p className="text-lg text-gray-700">در حال بارگذاری...</p>
      </div>
    );
  }

  // Unauthenticated: redirect to login
  if (!role) {
    return <Navigate to="/auth" replace />;
  }

  // Restrict access to role-specific routes
  const adminOnlyPaths = [
    "/dashboard/products",
    "/dashboard/users",
    "/dashboard/blogs",
  ];
  const isAdminPath = adminOnlyPaths.some((path) =>
    location.pathname.startsWith(path)
  );
  if (role === "user" && isAdminPath) {
    return <Navigate to="/dashboard/orders" replace />;
  }

  const userOnlyPaths = ["/dashboard/profile"];
  const isUserPath = userOnlyPaths.some((path) =>
    location.pathname.startsWith(path)
  );
  if ((role === "admin" || role === "manager") && isUserPath) {
    return <Navigate to="/dashboard" replace />;
  }

  return (
    <div
      dir="rtl"
      className={`min-h-screen bg-gray-50 dark:bg-neutral-900 flex text-right font-vazir`}
    >
      {/* Sidebar */}
      <aside className="w-64 bg-white dark:bg-neutral-800 shadow-lg hidden md:block transition-all">
        <div className="p-6 font-bold text-lg text-blue-600 dark:text-white">
          {role === "user" ? "داشبورد کاربر" : "مدیریت"}
        </div>
        <nav className="space-y-2 px-4">
          {navItems.map((item, idx) => (
            <Link
              key={idx}
              to={item.path}
              className={`w-full flex items-center justify-start gap-2 text-sm px-3 py-2 rounded-xl transition
                ${
                  isActive(item.path)
                    ? "bg-blue-100 text-blue-600 dark:bg-neutral-700 dark:text-white"
                    : "text-gray-700 dark:text-gray-200 hover:bg-blue-100 dark:hover:bg-neutral-700"
                }`}
            >
              {item.icon}
              <span>{item.label}</span>
            </Link>
          ))}
        </nav>
      </aside>

      {/* Main */}
      <div className="flex-1 flex flex-col">
        {/* Topbar */}
        <header className="flex items-center justify-between px-4 py-3 border-b bg-white dark:bg-neutral-800 dark:border-neutral-700 shadow-sm">
          <div className="md:hidden">
            <button
              onClick={() => setSidebarOpen(!sidebarOpen)}
              className="text-gray-700 dark:text-white"
            >
              ☰
            </button>
          </div>
          <div className="flex items-center gap-4">
            <img
              src="/api/placeholder/30/30"
              alt="کاربر"
              className="w-8 h-8 rounded-full border"
            />
            <LogOut
              size={18}
              className="text-red-500 cursor-pointer"
              onClick={handleLogout}
              aria-label="خروج"
            />
          </div>
        </header>

        {/* Content */}
        <main className="p-6">{children}</main>
      </div>

      {/* Mobile Sidebar */}
      <AnimatePresence>
        {sidebarOpen && (
          <motion.aside
            initial={{ x: "100%" }}
            animate={{ x: 0 }}
            exit={{ x: "100%" }}
            className="fixed top-0 right-0 w-64 h-full bg-white dark:bg-neutral-800 shadow-lg z-50 p-6 md:hidden"
          >
            <div className="flex justify-between items-center mb-6">
              <span className="text-blue-600 dark:text-white font-bold text-lg">
                منو
              </span>
              <button
                onClick={() => setSidebarOpen(false)}
                className="text-gray-600 dark:text-white"
              >
                ✕
              </button>
            </div>
            <nav className="space-y-2">
              {navItems.map((item, idx) => (
                <Link
                  key={idx}
                  to={item.path}
                  onClick={() => setSidebarOpen(false)}
                  className={`w-full flex items-center justify-start gap-2 text-sm px-3 py-2 rounded-xl transition
                    ${
                      isActive(item.path)
                        ? "bg-blue-100 text-blue-600 dark:bg-neutral-700 dark:text-white"
                        : "text-gray-700 dark:text-gray-200 hover:bg-blue-100 dark:hover:bg-neutral-700"
                    }`}
                >
                  {item.icon}
                  <span>{item.label}</span>
                </Link>
              ))}
            </nav>
          </motion.aside>
        )}
      </AnimatePresence>
    </div>
  );
};

export default DashboardLayout;
